<template lang="pug">
  
  .note.full-screen
    .note-header
      .note-header__back(@click='goBack')
        svg
          use(xlink:href='#arrow')
        p Заметки
      
      p(@click='save').note-header__save Сохранить

    .note-text
      input(type='text' placeholder='Заголовок заметки' v-model='title').note-text__title 
      textarea(placeholder='Текст заметки' v-model='text').note-text__text
      

</template>

<script>

  import {required} from 'vuelidate/lib/validators'

  import {mapState} from 'vuex'

  export default {
    props: {
      time: [Number, String]
    },
    data() {
      return {
        title: '',
        text: ''
      }
    },
    validations: {
      title: {required},
      text: {required}
    },
    computed: mapState({
      notes: state => state.notes.notesList
    }),
    methods: {
      save() {
        this.$v.$touch()

        if (this.$v.$error)
          return this.$alert.show('Заполните все поля')

        this.$store.commit('SAVE_NOTE', {name: this.title, text: this.text, time: new Date().getTime(), checkTime: this.time})
        this.$alert.show('Заметка сохранена')
        this.goBack()
      },
      goBack() {
        this.$router.go(-1)
      }
    },
    created() {

      if (this.time != 0) {
        let findNote = this.notes.find(item => item.time == this.time)

        this.title = findNote.name 
        this.text = findNote.text 

      } 

      this.$store.commit('REFRESH_SYSTEM_STYLE', true)
    }
  }

</script>


<style lang="sass"> @import '@/assets/sass/phone/apps/notes/main' </style>